<template>
    <div class="mod-sidebar">
        <div class="sidebar-toggle-out" v-if="showTag">
            <a class="pointer btn-toggle-in" @click="handleChangeLoggle()"></a>
            <div class="sidebar-content">
                <div class="sidebar-inner">
                    <div class="box-menu">
                        <ul>
                            <!-- 
                            <li>
                                <a class="menu-item pointer toggle-menu mine-item">
                                    <i class="icon icon-user"></i>我的
                                </a>
                                <div class="game-catalog menu-content">
                                    <div class="menu-wrap">
                                        <a href="http://yun.duowan.com/user/watch" class="game" data-href="" data-slink="ubook"><i class="icon icon-ubook"></i>我的订阅</a>
                                        <a href="http://yun.duowan.com/user/favorite" class="game" data-href="" data-slink="ufavo"><i class="icon icon-ufavo"></i>我的收藏</a>
                                        <a href="http://yun.duowan.com/user/focus" class="game" data-href="" data-slink="ufocus"><i class="icon icon-ufocus"></i>我的关注</a>
                                    </div>
                                </div>
                            </li><li><a href="//yun.duowan.com/user/history" data-href="//yun.duowan.com/user/history" class="menu-item"><i class="history"></i>观看历史</a></li> 
                            -->
                            <li>
                                <a class="menu-item pointer toggle-menu game-item  current">
                                    <i class="icon icon-filter"></i>视频分类
                                </a>
                                <div class="filter-catalog menu-content" style="height:800px;overflow-y:scroll;">
                                    <div class="menu-wrap">
                                        
                                        <h4 class="type-name">&nbsp;&nbsp;</h4>
                                        <!--
                                        <ul>
                                        <li v-if="loadTag">
                                            <i class="fa fa-refresh fa-spin fa-3x fa-fw"></i>
                                            <span class="sr-only">Loading...</span>
                                        </li>    
                                        </ul>
                                        -->
                                        <ul class="sub-type item-time">   
                                            <li  v-for="site in tagData" style="width:auto;">
                                                  <router-link :to="'/lista'+site.name">
                                                    <a href="javascript:void(0);" v-bind:class="{ active: site.name == tag }" style="width:auto;overflow:hidden;" :title="site.cname">{{site.cname}}</a>
                                                  </router-link>   
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <!--  
                    <div class="box-ft">
                        <div class="btn-wrap">
                            <a data-href="" href="http://yun.duowan.com/dl/" class="btn-download" target="_blank">
                                <i></i>云玩APP</a>
                    
                          
                            <a target="_blank" href="http://shang.qq.com/wpa/qunwpa?idkey=487ca5e53ef01b04a5f88a022ccf2aee79a6483bc160080b1cab79894458bedd" class="btn-qq">
                                <i></i>联系我们</a>
                    
                        </div>
                    </div>
                    -->
                </div>
            </div>
        </div>
        <div class="sidebar-toggle-in"  v-if="!showTag" style="display:block;width:0px;">
            <a class="pointer btn-toggle-out" @click="handleChangeLoggle(1)"></a>
            <div class="sidebar-inner">
                <ul>
                    <!--
                    <li>
                        <a href="http://yun.duowan.com/user/watch" data-href="//yun.duowan.com/user/watch" data-slink="ubook">
                            <i class="icon icon-ubook"></i>
                            <p>云玩APP</p>
                        </a>
                    </li>
                    <li>
                        <a href="http://yun.duowan.com/user/favorite" data-href="//yun.duowan.com/user/favorite" data-slink="ufavo">
                            <i class="icon icon-ufavo"></i>
                            <p>收藏</p>
                        </a>
                    </li>
                    <li>
                        <a href="http://yun.duowan.com/user/focus" data-href="//yun.duowan.com/user/focus" data-slink="ufocus">
                            <i class="icon icon-ufocus"></i>
                            <p>关注</p>
                        </a>
                    </li>
                    <li>
                        <a class="pointer to-toggle" data-toggle="filter">
                            <i class="icon icon-filter"></i>
                            <p>筛选</p>
                        </a>
                    </li>
                    -->
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'Slidebar',
        data() {
            return {
                url: this.GLOBAL.serviceUrl+'?t=2',
                tagUrl: this.GLOBAL.serviceUrl+'?t=3',
                tableData: [],
                total:0,
                cur_page: 1,
                pageSize:0,
                pageList:[],
                tData:[],
                showTag:1,
                loadTag:1,
                tag:this.$route.params.tag
            }
        },
        created() {
            //this.getData();
            this.getTagData();
        },
        mounted(){
            //this.getData();
        },
        watch: {
          '$route' (to,from) {
            this.tag = this.$route.params.tag
            this.loadTag = 1
            //this.getData();
            //this.getTagData();
          }
        },  
        computed: {
            data() {
                return this.tableData             
            },
            curPageList() {
                return this.pageList             
            },
            tagData() {
                return this.tData
            },
            "msg"(){
                return "asdff"
            } 
        },
        methods: {
            handleChangeLoggle(type=0){
                this.showTag = type
                this.$emit('child_event',type)
            },
            refresh(){
                this.getData();
            },
            curTitle(val){
                let obj = {};

            　　obj = this.role_list.find((item)=>{

            　　　　return item.id === val;

            　　});
                this.selected_title=obj.title
            },
            // 分页导航
            handleCurrentChange(val) {
                this.cur_page = val;
                this.getData();
            },
            getTagData() {
                var a=this.commonfunc._getRandom()
                var nonce = parseInt(a)
                var token=this.commonfunc._gtokene(nonce)
                var that = this  
                this.axios.post(this.tagUrl,this.qs.stringify({a: 1}),{
                        headers: {'Content-Type':'application/x-www-form-urlencoded','nonce':nonce,'token':token}
                    }).then(function(res){
                    if(parseInt(res.data.message)==0){                  
                       that.tData = res.data.data;   
                    }
                    that.loadTag = 0       
                }).catch(function(err){
                 console.log(err)
                })
                              
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                var a=this.commonfunc._getRandom()
                var nonce = parseInt(a)
                var token=this.commonfunc._gtokene(nonce)
                var that = this  
                this.axios.post(this.url,this.qs.stringify({page: this.cur_page}),{
                        headers: {'Content-Type':'application/x-www-form-urlencoded','nonce':nonce,'token':token}
                    }).then(function(res){
                    if(parseInt(res.data.message)==0){                  
                       that.tableData = res.data.data; 
                       that.total=res.data.count
                       that.pageList=res.data.page    
                    }
                    //console.log(that.total)
                    //that.total=res.        
                }).catch(function(err){
                 console.log(err)
                })
                              
            },
            search() {
                this.is_search = true;
                this.cur_page = 1;
                this.getData();
            },
            formatter(row, column) {
                return row.address;
            },
            filterTag(value, row) {
                return row.tag === value;
            },
            // 确定删除
            deleteRow(){  
                var that = this
                this.axios.post(this.GLOBAL.serviceUrl+'a_user_del',this.qs.stringify({id: this._delId}),{
                        headers: {'token': that.commonfunc.get('token')}
                    }).then(function(res){
                    var msg = '删除成功'
                    if('400'!=res.data.ret){
                        that.$message.success(msg);
                    }else{
                        that.$message.error(res.data.msg);
                    }
                    that.delVisible = false;
                    that.tableData.splice(that.idx, 1);
                    //console.log(res)
                    //console.log(res.data.data.list)
                    //that.tableData = res.data.data.list;
                    //that.total=res.        
                }).catch(function(err){
                 console.log(err)
                })             
                //this.tableData.splice(this.idx, 1);

            },
            strLen(str){
                var len = 0;
                for (var i=0; i<str.length; i++) { 
                 var c = str.charCodeAt(i); 
                //单字节加1 
                 if ((c >= 0x0001 && c <= 0x007e) || (0xff60<=c && c<=0xff9f)) { 
                   len++; 
                 } 
                 else { 
                  len+=2; 
                 } 
                } 
                return len;           
            },
        }
    }

</script>
<style scope>
.mod-sidebar .sidebar-toggle-out{
    width:250px;
}
</style>
